<template>
        <div class="w-100 float-left">
            <div class="row">
                <div class="w-100 float-left">
                    <dynamic-form v-if="isLoaded" :schema="FormSchema" :buttons="buttons" v-on:OnSubmit="onSubmit">
                    </dynamic-form>
                </div>
            </div>
        </div>
</template>

<script>
import DataServices from "../../../services/DataService";
export default {
    data(){
        return {
            FormSchema:[
                {
                    layoutType: "double",
                    Data:[
                        {
                            astype: "MultiSelectField",
                            mode: "tag",
                            label: this.$t('Contacts'),
                            name: "Contacts",
                            value: "",
                            placeholder: this.$t('Contacts'),
                            config: {
                                options:[]
                            },
                            validationRules: { "required": true }
                        },
                        {
                            astype: "TextAreaField",
                            label: this.$t('Content'),
                            name: "Content",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true }
                        }
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success save",
                    text: "<i class='fa fa-send pr-2'></i>Send"
                }
            ],
            isLoaded:0
        }
    },
    created(){
        var vm=this;
        debugger;
         vm.GetContacts();
         vm.isLoaded=1;

    },
    methods:{
        onSubmit(formfields){
            var sendTo = '';
            formfields.Contacts.forEach((item,index) => {
                index==0 ? sendTo=item.value : sendTo += ','+item.value;
            });
            var obj={
                sms_body : formfields.Content,
                send_to : sendTo
            }
            var url = JSON.stringify(obj);
            DataServices.SendTwilioSMS(url).then(function(response){
                debugger;
            })
        },
        GetContacts:  function () {
            var vm = this;
            var url = `id=&moduleName=CRM&type=CRM_CONTACT_NUMBERS&search=&refId=`;
            DataServices.getDDLData(url).then(function (response) {
                vm.FormSchema[0].Data[0].config.options=[];
                var Contactlist = response.data.DATA;
                Contactlist.forEach((item, index) =>{
                vm.FormSchema[0].Data[0].config.options.push({
                        name: item.name,
                        value: item.value
                    })
                })
            });
        }
    }
}
</script>
